import React from 'react';
import { 
  Card, 
  CardBody, 
  CardTitle, 
  CardText, 
  CardImg, 
  Col, 
  Row,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap';
import './Home.css';
var Airtable = require('airtable');
var base = new Airtable({apiKey: 'keyI3mce75kzQtsZF'}).base('appGrEpAVZejjCvEj');

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      allItems: '',
      selectedFilter: 'Most Popular'
    };
  }

  componentWillMount() {
    const items = [];
    const that  = this;
    base('ARG Data').select({
      maxRecords: 500,
      view: "Grid view"
    }).eachPage(function page(records, fetchNextPage) {
        records.forEach(function(record) {
          items.push(record.fields)
        });
        fetchNextPage();
    
    }, function done(err) {
        if (err) { 
          console.error(err); return; 
        } else {
          that.filterItems(items, 'Most Popular ');
          that.setState({
            allItems: items
          })
        }
    });
  }

  filterItems(items, filteredBy) {
    const filteredItems = items.filter(item => item.Category.includes(filteredBy.toLowerCase()));
    this.setState({
      products: filteredItems,
      selectedFilter: filteredBy
    })
  }

  showProduct(item) {
    this.props.history.push('/alexandra-rose-gifts/product', {
      item
    })
  }

  render() {
    const { products, allItems, selectedFilter } = this.state;
    return (
        <section>
          <h1>{selectedFilter}</h1>
          <Row>
            <UncontrolledDropdown>
              <DropdownToggle nav caret>
                Filter
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem onClick={() => this.filterItems(allItems, 'Bottles')}>
                  Bottles
                </DropdownItem>
                <DropdownItem onClick={() => this.filterItems(allItems, 'Cleaning')}>
                  Cleaning
                </DropdownItem>
                <DropdownItem onClick={() => this.filterItems(allItems, 'Gifts')}>
                  Gifts
                </DropdownItem>
                <DropdownItem onClick={() => this.filterItems(allItems, 'Health and Beauty ')}>
                  Health and Beauty
                </DropdownItem>
                <DropdownItem onClick={() => this.filterItems(allItems, 'Most Popular ')}>
                  Most Popular
                </DropdownItem>
                <DropdownItem onClick={() => this.filterItems(allItems, 'Special Occasion')}>
                  Special Occasion
                </DropdownItem>
                <DropdownItem onClick={() => this.filterItems(allItems, 'Storage')}>
                  Storage
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Row>
          <Row style={{marginTop: '1rem'}}>
          {products && products.map(
            (item, i) =>
            <Col sm="6" md="4" lg="3" key={i}>
              <Card color="secondary" onClick={() => this.showProduct(item)} style={{ borderColor: '#421791' }}>
                <CardImg className="home-images" top width="100%" src={item["Display image"][0].url} alt={item.Product} />
                <CardBody>
                  <CardTitle><h3>{item.Product}</h3></CardTitle>
                  <CardText className="description-text">{item.Description}</CardText>
                  <CardText>
                    <small>£{item.Price}</small>
                  </CardText>
                </CardBody>
              </Card>
            </Col>
          )}
          </Row>
        </section>
    );
  }
}
export default Home;