import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Home from './pages/Home';
import About from './pages/About';
import Basket from './pages/Basket';
import ProductShow from './pages/ProductShow';
import Header from './components/Header';
import Footer from './components/Footer';
import './App.css';

function App() {
  return (
    <main>
      <Router>
        <Header />
        <Switch>
          <Route path="/alexandra-rose-gifts/basket" component={Basket} exact/>
          <Route path="/alexandra-rose-gifts/product" component={ProductShow} exact/>
          <Route path="/alexandra-rose-gifts/about" component={About} exact/>
          <Route path="/alexandra-rose-gifts" component={Home} exact/>
          <Route path="/"><Redirect to="/alexandra-rose-gifts" /></Route>
        </Switch>      
      </Router>
      <Footer />
    </main>
  );
}

export default App;
